.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 50px;
}
.KSP_Ent,
.KSP_Eng {
  /* border: 2px solid #000; */
  font-weight: 900;
  font-size: 2rem;
  width: 500px;
  margin-left: 100px;
  cursor: pointer;
  background: rgb(5, 186, 172);
  background: linear-gradient(
    90deg,
    rgba(5, 186, 172, 1) 8%,
    rgba(231, 253, 0, 1) 100%
  );
  text-transform: uppercase;
  transition: background-color 1s ease-in-out;
}
.KSP_Ent:hover,
.KSP_Eng:hover {
  background: rgb(0, 188, 253);
  background: linear-gradient(
    90deg,
    rgba(0, 188, 253, 1) 17%,
    rgba(56, 198, 187, 1) 85%
  );
}
