.KSP_Ent_about,
.KSP_Eng_about {
  /* border: 2px solid #000; */
  font-weight: 400;
  font-size: 2rem;
  margin-left: 100px;
  cursor: pointer;
  background: rgb(5, 186, 172);
  background: linear-gradient(
    90deg,
    rgba(5, 186, 172, 1) 8%,
    rgba(231, 253, 0, 1) 100%
  );
  transition: background-color 1s ease-in-out;
  animation: slideInFromLeft 1s ease-in-out forwards; /* Apply the animation */
}
.KSP_Ent_about {
  animation: slideInFromLeft 1s ease-in-out forwards; /* Apply the animation */
}
.KSP_Eng_about {
  animation: slideInFromRight 1s ease-in-out forwards; /* Apply the animation */
}
.KSP_Ent_about {
  margin-top: 10px;
  margin-bottom: 50px;
  margin-right: 20px;
}
.KSP_Eng_about {
  margin-top: 10px;
  margin-bottom: 50px;
  margin-right: 20px;
}
.KSP_Ent_about .name,
.KSP_Eng_about .name {
  text-transform: uppercase;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 2.5rem;
  font-weight: bolder;
}
.KSP_Ent:hover,
.KSP_Eng:hover {
  background: rgb(0, 188, 253);
  background: linear-gradient(
    90deg,
    rgba(0, 188, 253, 1) 17%,
    rgba(56, 198, 187, 1) 85%
  );
}

.address {
  position: relative;
}
.location {
  position: absolute;
  top: 4px;
  left: 90px;
}
.Ksp_email {
  position: relative;
}
.email {
  position: absolute;
  top: 5px;
  left: 450px;
}
.email1 {
  position: absolute;
  top: 50px;
  left: 450px;
}
.about_title {
  font-weight: 600;
}
@keyframes slideInFromRight {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }
  5% {
    opacity: 0;
    transform: translateX(50%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
